
import Page from 'components/Page';

import React,{useState} from 'react';
import {
  Button,
  Card,
  CardBody,
  CardImg,
  CardText,
  CardTitle,
  Col,
  Modal,
  ModalBody,
  ModalFooter,
  ModalHeader,
  Carousel,
  CarouselItem,
  CarouselControl,
  CarouselIndicators,
  Row
} from 'reactstrap';
const items = [
  {
    src: '/img/carousel/1.jpg',
    altText: 'Slide 1'
  },
  {
    src: '/img/carousel/2.jpg',
    altText: 'Slide 2'
  },
  {
    src: '/img/carousel/3.jpg',
    altText: 'Slide 3'
  }
];
const DashboardPage =(props)=> {
  var [modal,setModal]=useState(false);
  var [listNo,setListNo]=useState(0);
  //carousel
  const [activeIndex, setActiveIndex] = useState(0);
  const [animating, setAnimating] = useState(false);

  const next = () => {
    if (animating) return;
    const nextIndex = activeIndex === items.length - 1 ? 0 : activeIndex + 1;
    setActiveIndex(nextIndex);
  }

  const previous = () => {
    if (animating) return;
    const nextIndex = activeIndex === 0 ? items.length - 1 : activeIndex - 1;
    setActiveIndex(nextIndex);
  }

  const goToIndex = (newIndex) => {
    if (animating) return;
    setActiveIndex(newIndex);
  }

 
  const slides = items.map((item) => {
    return (
      <CarouselItem
        onExiting={() => setAnimating(true)}
        onExited={() => setAnimating(false)}
        key={item.src}
      >
        <img src={item.src} alt={item.altText} />
      </CarouselItem>
    );
  });
  //products
  var list=[{
    img:'/img/products/products (1).jpg',
    title:`Helix Analog Black Dial Women's Watch-TW029HL07`,
    text:`₹ 1635.00`
  },{
    img:'/img/products/products (2).jpg',
    title:`Seiko Analog White Dial Men's Watch - SGEH78P1`,
    text:`₹ 1635.00`
  },{
    img:'/img/products/products (3).jpg',
    title:`Seiko Analog White Dial Men's Watch - SGEH78P1`,
    text:`₹ 1635.00`
  },{
    img:'/img/products/products (4).jpg',
    title:`Seiko Analog White Dial Men's Watch - SGEH78P1`,
    text:`₹ 1635.00`
  },{
    img:'/img/products/products (5).jpg',
    title:`Seiko Analog White Dial Men's Watch - SGEH78P1`,
    text:`₹ 1635.00`
  },{
    img:'/img/products/products (6).jpg',
    title:`Seiko Analog White Dial Men's Watch - SGEH78P1`,
    text:`₹ 1635.00`
  },{
    img:'/img/products/products (7).jpg',
    title:`Seiko Analog White Dial Men's Watch - SGEH78P1`,
    text:`₹ 1635.00`
  },{
    img:'/img/products/products (8).jpg',
    title:`Seiko Analog White Dial Men's Watch - SGEH78P1`,
    text:`₹ 1635.00`
  },{
    img:'/img/products/products (9).jpg',
    title:`Seiko Analog White Dial Men's Watch - SGEH78P1`,
    text:`₹ 1635.00`
  },{
    img:'/img/products/products (10).jpg',
    title:`Seiko Analog White Dial Men's Watch - SGEH78P1`,
    text:`₹ 1635.00`
  },{
    img:'/img/products/products (11).jpg',
    title:`Seiko Analog White Dial Men's Watch - SGEH78P1`,
    text:`₹ 1635.00`
  },{
    img:'/img/products/products (12).jpg',
    title:`Seiko Analog White Dial Men's Watch - SGEH78P1`,
    text:`₹ 1635.00`
  },{
    img:'/img/products/products (13).jpg',
    title:`Seiko Analog White Dial Men's Watch - SGEH78P1`,
    text:`₹ 1635.00`
  },{
    img:'/img/products/products (14).jpg',
    title:`Seiko Analog White Dial Men's Watch - SGEH78P1`,
    text:`₹ 1635.00`
  }];
  return (

    <Page
      className="DashboardPage"    
    >
      {/* <Carousel >

      </Carousel> */}
      <Carousel style={{'height':'300px'}}
        activeIndex={activeIndex}
        next={next}
        previous={previous}
      >
        <CarouselIndicators items={items} activeIndex={activeIndex} onClickHandler={goToIndex} />
        {slides}
        <CarouselControl direction="prev" directionText="Previous" onClickHandler={previous} />
        <CarouselControl direction="next" directionText="Next" onClickHandler={next} />
      </Carousel>
      
      <Row className={'mt-2 ml-2 mr-2'}>
        {
          list.map((ele,key)=>(
            <Col md={2} sm={3} xs={6} className="mb-3 pl-2 pr-2" key={key}>
              <Card className={'list-products'}>
                <CardImg top src={ele.img} />
                <CardBody>
                  <CardTitle onClick={()=>{setListNo(key);setModal(true);}} >{ele.title}</CardTitle>
                  <CardText>
                    {ele.text}
                  </CardText>
                </CardBody>
              </Card>
            </Col>
          ))
        }    
      
      </Row>
      <Row>
        <div style={{"height":'60px'}}></div>
      </Row>
      <Modal
        isOpen={modal}
        toggle={()=>setModal(!modal)}
        className={props.className}>
        <ModalHeader toggle={()=>setModal(!modal)}>{list[listNo].title}</ModalHeader>
        <ModalBody>
          <Row>     
            <img src={list[listNo].img} ></img>  
            {list[listNo].text}                    
          </Row>
          
        </ModalBody>
        <ModalFooter>
          <Button color="primary" onClick={()=>setModal(!modal)}>
            OK
          </Button>{' '}
          <Button color="secondary" onClick={()=>setModal(!modal)}>
            Cancel
          </Button>
        </ModalFooter>
      </Modal>
      
      
    </Page>
  );
 
}
export default DashboardPage;
